<template>
  <div class="card">
    <div class="card-header">
      <h3 class="card-title flex items-center">
        <component 
          :is="statusIcon" 
          :class="['w-5 h-5 mr-2', statusColor]"
        />
        ç³»ç»ŸçŠ¶æ€
      </h3>
    </div>
    
    <div class="space-y-4">
      <!-- å½“å‰çŠ¶æ€ -->
      <div class="flex items-center justify-between">
        <span class="text-gray-600">è¿è¡ŒçŠ¶æ€</span>
        <span :class="['status-indicator', statusClass]">
          {{ statusText }}
        </span>
      </div>
      
      <!-- å½“å‰ä¼šè¯ -->
      <div class="flex items-center justify-between">
        <span class="text-gray-600">å½“å‰ä¼šè¯</span>
        <span class="font-medium text-gray-900">
          {{ currentSessionDisplay }}
        </span>
      </div>
      
      <!-- è¿è¡Œæ—¶é—´ -->
      <div class="flex items-center justify-between">
        <span class="text-gray-600">è¿è¡Œæ—¶é—´</span>
        <span class="font-medium text-gray-900">
          {{ runningTimeDisplay }}
        </span>
      </div>
      
      <!-- è¿›åº¦ç™¾åˆ†æ¯”ï¼ˆå¦‚æœæœ‰ï¼‰ -->
      <div 
        v-if="systemStatus?.progress_percentage && systemStatus.progress_percentage > 0" 
        class="space-y-2"
      >
        <div class="flex items-center justify-between text-sm">
          <span class="text-gray-600">å¤„ç†è¿›åº¦</span>
          <span class="font-medium text-gray-900">
            {{ Math.round(systemStatus.progress_percentage) }}%
          </span>
        </div>
        <div class="w-full bg-gray-200 rounded-full h-2">
          <div 
            class="bg-primary-500 h-2 rounded-full transition-all duration-500 ease-out"
            :style="{ width: `${systemStatus.progress_percentage}%` }"
          ></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed } from 'vue'
import { useMonitoringStore } from '@/stores/monitoring'
import { formatDuration } from '@/utils/formatters'

// å›¾æ ‡å¯¼å…¥
import {
  PlayIcon,
  PauseIcon,
  ExclamationTriangleIcon,
  WifiIcon
} from '@heroicons/vue/24/outline'

const monitoringStore = useMonitoringStore()

// è®¡ç®—å±æ€§
const systemStatus = computed(() => monitoringStore.systemStatus)

const statusIcon = computed(() => {
  if (!systemStatus.value) return WifiIcon
  
  switch (systemStatus.value.status) {
    case 'running':
      return PlayIcon
    case 'idle':
      return PauseIcon
    case 'error':
      return ExclamationTriangleIcon
    default:
      return WifiIcon
  }
})

const statusColor = computed(() => {
  if (!systemStatus.value) return 'text-gray-500'
  
  switch (systemStatus.value.status) {
    case 'running':
      return 'text-green-500'
    case 'idle':
      return 'text-gray-500'
    case 'error':
      return 'text-red-500'
    default:
      return 'text-gray-500'
  }
})

const statusClass = computed(() => {
  if (!systemStatus.value) return 'status-idle'
  
  switch (systemStatus.value.status) {
    case 'running':
      return 'status-running'
    case 'idle':
      return 'status-idle'
    case 'error':
      return 'status-error'
    default:
      return 'status-idle'
  }
})

const statusText = computed(() => {
  if (!systemStatus.value) return 'ç¦»çº¿'
  
  switch (systemStatus.value.status) {
    case 'running':
      return 'è¿è¡Œä¸­'
    case 'idle':
      return 'ç©ºé—²'
    case 'error':
      return 'é”™è¯¯'
    case 'offline':
      return 'ç¦»çº¿'
    default:
      return 'æœªçŸ¥'
  }
})

const currentSessionDisplay = computed(() => {
  if (!systemStatus.value?.session_id) {
    return 'æ— '
  }
  
  const sessionId = systemStatus.value.session_id
  // æ˜¾ç¤ºä¼šè¯IDçš„å‰8ä½
  return sessionId.length > 8 ? `${sessionId.substring(0, 8)}...` : sessionId
})

const runningTimeDisplay = computed(() => {
  if (!systemStatus.value?.running_time) {
    return '0ç§’'
  }
  
  return formatDuration(systemStatus.value.running_time)
})
</script>