// æ¨¡æ‹Ÿå…³é”®è¯æ•°æ®
const mockKeywords = ref<KeywordData[]>([
  { word: 'ç›‘æ§', count: 45, frequency: 0.8 },
  { word: 'æ€§èƒ½', count: 38, frequency: 0.68 },
  { word: 'æ•°æ®', count: 32, frequency: 0.57 },
  { word: 'ç³»ç»Ÿ', count: 28, frequency: 0.5 },
  { word: 'å®æ—¶', count: 25, frequency: 0.45 },
  { word: 'åˆ†æ', count: 22, frequency: 0.39 },
  { word: 'ä¼šè¯', count: 20, frequency: 0.36 },
  { word: 'ç¼“å­˜', count: 18, frequency: 0.32 },
  { word: 'ä¼˜åŒ–', count: 15, frequency: 0.27 },
  { word: 'å“åº”', count: 12, frequency: 0.21 },
  { word: 'æŸ¥è¯¢', count: 10, frequency: 0.18 },
  { word: 'è®°å¿†', count: 8, frequency: 0.14 },
  { word: 'æµç¨‹', count: 6, frequency: 0.11 },
  { word: 'è¯„ä¼°', count: 5, frequency: 0.09 },
  { word: 'ä¸Šä¸‹æ–‡', count: 4, frequency: 0.07 }
])

// è®¡ç®—å±æ€§
const keywords = computed(() => {
  return monitoringStore.keywords?.top_keywords || mockKeywords.value
})

const hasKeywords = computed(() => keywords.value.length > 0)

const totalKeywords = computed(() => {
  return monitoringStore.keywords?.total_unique_keywords || keywords.value.length
})

const displayKeywords = computed(() => {
  return keywords.value.slice(0, 15) // æœ€å¤šæ˜¾ç¤º15ä¸ªå…³é”®è¯
})

const topKeywords = computed(() => {
  return keywords.value.slice(0, 8) // æ˜¾ç¤ºå‰8ä¸ªå…³é”®è¯çš„ç»Ÿè®¡
})

const maxFrequency = computed(() => {
  return Math.max(...keywords.value.map(k => k.frequency))
})

// æ–¹æ³•
const getKeywordSize = (frequency: number): string => {
  const baseSize = 12
  const maxSize = 20
  const size = baseSize + (frequency * (maxSize - baseSize))
  return `${Math.round(size)}px`
}

const getKeywordClass = (frequency: number): string => {
  if (frequency >= 0.6) {
    return 'bg-red-100 text-red-800 hover:bg-red-200'
  } else if (frequency >= 0.4) {
    return 'bg-orange-100 text-orange-800 hover:bg-orange-200'
  } else if (frequency >= 0.2) {
    return 'bg-blue-100 text-blue-800 hover:bg-blue-200'
  } else {
    return 'bg-gray-100 text-gray-800 hover:bg-gray-200'
  }
}

const getRelevanceLevel = (frequency: number): string => {
  if (frequency >= 0.6) return 'é«˜'
  if (frequency >= 0.3) return 'ä¸­'
  return 'ä½'
}

const selectKeyword = (keyword: KeywordData) => {
  selectedKeyword.value = keyword
}

const refreshKeywords = async () => {
  if (isRefreshing.value) return
  
  try {
    isRefreshing.value = true
    
    // æ¨¡æ‹Ÿæ•°æ®åˆ·æ–°
    await new Promise(resolve => setTimeout(resolve, 1000))
    
    // æ¨¡æ‹Ÿå…³é”®è¯æ•°æ®æ›´æ–°
    mockKeywords.value = mockKeywords.value.map(keyword => ({
      ...keyword,
      count: keyword.count + Math.floor(Math.random() * 5),
      frequency: Math.min(1, keyword.frequency + (Math.random() - 0.5) * 0.1)
    }))
    
    // æŒ‰é¢‘ç‡é‡æ–°æ’åº
    mockKeywords.value.sort((a, b) => b.frequency - a.frequency)
    
  } finally {
    isRefreshing.value = false
  }
}

onMounted(() => {
  // åˆå§‹åŒ–æ—¶å¯ä»¥ä»ç›‘æ§å­˜å‚¨ä¸­è·å–çœŸå®æ•°æ®
  if (!hasKeywords.value) {
