const apiStatusBadgeClass = computed(() => {
  return monitoringStore.isConnected ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
})

// è®¡ç®—å±æ€§ - æ•°æ®æºçŠ¶æ€
const dataSourceIcon = computed(() => {
  switch (monitoringStore.dataSource) {
