const dataSourceColor = computed(() => {
  switch (monitoringStore.dataSource) {
    case 'live':
      return 'text-green-500'
    case 'test':
      return 'text-blue-500'
    default:
      return 'text-yellow-500'
  }
})

const dataSourceText = computed(() => {
  switch (monitoringStore.dataSource) {
    case 'live':
      return 'å®æ—¶æ•°æ®'
    case 'test':
      return 'æµ‹è¯•æ•°æ®'
    default:
      return 'æ¨¡æ‹Ÿæ•°æ®'
  }
})

const dataSourceBadgeClass = computed(() => {
  switch (monitoringStore.dataSource) {
    case 'live':
      return 'bg-green-100 text-green-800'
    case 'test':
      return 'bg-blue-100 text-blue-800'
    default:
      return 'bg-yellow-100 text-yellow-800'
  }
})

const dataSourceDescription = computed(() => {
  switch (monitoringStore.dataSource) {
    case 'live':
      return 'Estiaç³»ç»Ÿè¿è¡Œä¸­ï¼Œæ•°æ®å®æ—¶æ›´æ–°'
    case 'test':
      return 'ä½¿ç”¨æµ‹è¯•æ•°æ®è¿›è¡Œæ¼”ç¤º'
    default:
      return 'ä½¿ç”¨æ¨¡æ‹Ÿæ•°æ®ï¼ŒåŠŸèƒ½å—é™'
  }
})

// è¿æ¥è´¨é‡è¯„ä¼°
const connectionQuality = computed(() => {
  let quality = 0
  
  if (isConnected()) quality += 1
  if (monitoringStore.isConnected) quality += 1
  if (wsLatency.value < 100) quality += 1
  if (wsReconnectCount.value < 3) quality += 1
  
  return quality
})

const connectionQualityText = computed(() => {
  switch (connectionQuality.value) {
    case 4: return 'ä¼˜ç§€'
    case 3: return 'è‰¯å¥½'
    case 2: return 'ä¸€èˆ¬'
    case 1: return 'è¾ƒå·®'
    default: return 'æ–­å¼€'
  }
})

const lastHealthCheckDisplay = computed(() => {
  if (!lastHealthCheck.value) return 'ä»æœª'
  return formatRelativeTime(lastHealthCheck.value)
})

// æ–¹æ³•
const handleReconnect = async () => {
  try {
    await connect()
    
    appStore.addNotification({
      type: 'success',
      title: 'é‡è¿æˆåŠŸ',
      message: 'WebSocketè¿æ¥å·²æ¢å¤',
      duration: 3000
    })
    
    wsReconnectCount.value++
  } catch (error) {
    appStore.addNotification({
      type: 'error',
      title: 'é‡è¿å¤±è´¥',
      message: 'æ— æ³•å»ºç«‹WebSocketè¿æ¥',
      duration: 5000
    })
  }
}

const runConnectionTest = async () => {
  if (isTestingConnection.value) return
  
  try {
    isTestingConnection.value = true
    
    // æµ‹è¯•APIè¿æ¥
    const startTime = Date.now()
    const isHealthy = await apiService.checkHealth()
    const endTime = Date.now()
    
    apiResponseTime.value = endTime - startTime
    lastHealthCheck.value = new Date()
    
    // æµ‹è¯•WebSocketå»¶è¿Ÿ
    if (isConnected()) {
      const wsStart = Date.now()
      // å‘é€pingæ¶ˆæ¯æµ‹è¯•å»¶è¿Ÿ
      setTimeout(() => {
        wsLatency.value = Date.now() - wsStart
      }, 50)
    }
    
    const testResult = {
      api: isHealthy,
      websocket: isConnected(),
      latency: apiResponseTime.value
    }
    
    appStore.addNotification({
      type: testResult.api && testResult.websocket ? 'success' : 'warning',
      title: 'è¿æ¥æµ‹è¯•å®Œæˆ',
      message: `API: ${testResult.api ? 'æ­£å¸¸' : 'å¼‚å¸¸'}, WebSocket: ${testResult.websocket ? 'æ­£å¸¸' : 'å¼‚å¸¸'}, å»¶è¿Ÿ: ${testResult.latency}ms`,
      duration: 5000
    })
    
  } catch (error) {
    console.error('è¿æ¥æµ‹è¯•å¤±è´¥:', error)
    appStore.addNotification({
      type: 'error',
      title: 'æµ‹è¯•å¤±è´¥',
      message: 'æ— æ³•å®Œæˆè¿æ¥æµ‹è¯•',
