import { format, formatDistanceToNow } from 'date-fns'
import { zhCN } from 'date-fns/locale'

/**
 * æ ¼å¼åŒ–æ—¶é—´é•¿åº¦ï¼ˆç§’ï¼‰ä¸ºå¯è¯»å­—ç¬¦ä¸²
 */
export function formatDuration(seconds: number): string {
  if (seconds < 1) {
    return `${Math.round(seconds * 1000)}æ¯«ç§’`
  }
  
  if (seconds < 60) {
    return `${Math.round(seconds)}ç§’`
  }
  
  const minutes = Math.floor(seconds / 60)
  const remainingSeconds = Math.round(seconds % 60)
  
  if (minutes < 60) {
    return remainingSeconds > 0 
      ? `${minutes}åˆ†${remainingSeconds}ç§’`
      : `${minutes}åˆ†é’Ÿ`
  }
  
  const hours = Math.floor(minutes / 60)
  const remainingMinutes = minutes % 60
  
  if (hours < 24) {
    return remainingMinutes > 0 
      ? `${hours}å°æ—¶${remainingMinutes}åˆ†é’Ÿ`
      : `${hours}å°æ—¶`
  }
  
  const days = Math.floor(hours / 24)
  const remainingHours = hours % 24
  
  return remainingHours > 0 
    ? `${days}å¤©${remainingHours}å°æ—¶`
    : `${days}å¤©`
}

/**
 * æ ¼å¼åŒ–å­—èŠ‚å¤§å°
 */
export function formatBytes(bytes: number, decimals = 2): string {
  if (bytes === 0) return '0 B'
  
  const k = 1024
  const dm = decimals < 0 ? 0 : decimals
  const sizes = ['B', 'KB', 'MB', 'GB', 'TB', 'PB']
  
  const i = Math.floor(Math.log(bytes) / Math.log(k))
  
  return `${parseFloat((bytes / Math.pow(k, i)).toFixed(dm))} ${sizes[i]}`
}

/**
 * æ ¼å¼åŒ–ç™¾åˆ†æ¯”
 */
export function formatPercentage(value: number, decimals = 1): string {
  return `${(value * 100).toFixed(decimals)}%`
}

/**
 * æ ¼å¼åŒ–æ•°å­—ï¼Œæ·»åŠ åƒåˆ†ä½åˆ†éš”ç¬¦
 */
export function formatNumber(num: number, decimals = 0): string {
  return new Intl.NumberFormat('zh-CN', {
    minimumFractionDigits: decimals,
    maximumFractionDigits: decimals,
  }).format(num)
}

/**
 * æ ¼å¼åŒ–å¤§æ•°å­—ä¸ºç´§å‡‘æ ¼å¼ (1000 -> 1K)
 */
export function formatCompactNumber(num: number): string {
  return new Intl.NumberFormat('zh-CN', {
    notation: 'compact',
    compactDisplay: 'short',
  }).format(num)
}

/**
 * æ ¼å¼åŒ–æ—¥æœŸæ—¶é—´
 */
export function formatDateTime(date: Date | string | number): string {
  const dateObj = typeof date === 'string' || typeof date === 'number' 
    ? new Date(date) 
    : date
    
  return format(dateObj, 'yyyy-MM-dd HH:mm:ss', { locale: zhCN })
}

/**
 * æ ¼å¼åŒ–ç›¸å¯¹æ—¶é—´ (2å°æ—¶å‰)
 */
export function formatRelativeTime(date: Date | string | number): string {
  const dateObj = typeof date === 'string' || typeof date === 'number' 
    ? new Date(date) 
    : date
    
  return formatDistanceToNow(dateObj, { 
    addSuffix: true, 
    locale: zhCN 
  })
}

/**
 * æ ¼å¼åŒ–æ—¶é—´æˆ³ä¸ºç®€çŸ­æ ¼å¼
 */
export function formatShortTime(timestamp: number): string {
  const date = new Date(timestamp)
  const now = new Date()
  
  // å¦‚æœæ˜¯ä»Šå¤©ï¼Œåªæ˜¾ç¤ºæ—¶é—´
  if (date.toDateString() === now.toDateString()) {
    return format(date, 'HH:mm', { locale: zhCN })
  }
  
  // å¦‚æœæ˜¯ä»Šå¹´ï¼Œæ˜¾ç¤ºæœˆæ—¥æ—¶é—´
  if (date.getFullYear() === now.getFullYear()) {
    return format(date, 'MM-dd HH:mm', { locale: zhCN })
  }
  
  // å¦åˆ™æ˜¾ç¤ºå®Œæ•´æ—¥æœŸ
  return format(date, 'yy-MM-dd HH:mm', { locale: zhCN })
}

/**
 * æˆªæ–­æ–‡æœ¬å¹¶æ·»åŠ çœç•¥å·
 */
export function truncateText(text: string, maxLength: number): string {
  if (text.length <= maxLength) return text
  return text.substring(0, maxLength) + '...'
}

/**
 * æ ¼å¼åŒ–JSONä¸ºå¯è¯»æ ¼å¼
 */
export function formatJSON(obj: any): string {
  try {
    return JSON.stringify(obj, null, 2)
  } catch (error) {
    return String(obj)
  }
}

/**
 * é«˜äº®æœç´¢å…³é”®è¯
 */
export function highlightSearchTerms(text: string, searchTerms: string[]): string {
  if (!searchTerms.length) return text
  
  let highlighted = text
  searchTerms.forEach(term => {
    const regex = new RegExp(`(${term})`, 'gi')
    highlighted = highlighted.replace(regex, '<mark class="bg-yellow-200 px-1 rounded">$1</mark>')
  })
  
  return highlighted
}

/**
 * ç”Ÿæˆéšæœºé¢œè‰²ï¼ˆç”¨äºå›¾è¡¨ç­‰ï¼‰
 */
export function generateRandomColor(alpha = 1): string {
  const hue = Math.floor(Math.random() * 360)
  const saturation = Math.floor(Math.random() * 30) + 70 // 70-100%
  const lightness = Math.floor(Math.random() * 20) + 45  // 45-65%
  
  return `hsla(${hue}, ${saturation}%, ${lightness}%, ${alpha})`
}

/**
 * ç”Ÿæˆé¢„å®šä¹‰çš„é¢œè‰²è°ƒè‰²æ¿
 */
export function getColorPalette(index: number): string {
  const colors = [
    '#3B82F6', // blue
    '#10B981', // green
    '#F59E0B', // yellow
    '#EF4444', // red
    '#8B5CF6', // purple
    '#06B6D4', // cyan
    '#F97316', // orange
    '#84CC16', // lime
    '#EC4899', // pink
    '#6B7280', // gray
  ]
  
  return colors[index % colors.length]
}

/**
 * è®¡ç®—æ–‡æœ¬çš„å“ˆå¸Œå€¼ï¼ˆç”¨äºç”Ÿæˆä¸€è‡´çš„é¢œè‰²ï¼‰
 */
export function hashStringToColor(str: string): string {
  let hash = 0
  for (let i = 0; i < str.length; i++) {
    const char = str.charCodeAt(i)
    hash = ((hash << 5) - hash) + char
    hash = hash & hash // 32ä½æ•´æ•°
  }
  
  const hue = Math.abs(hash) % 360
  return `hsl(${hue}, 70%, 50%)`
}

/**
 * æ·±æ‹·è´å¯¹è±¡
 */
export function deepClone<T>(obj: T): T {
  if (obj === null || typeof obj !== 'object') return obj
  if (obj instanceof Date) return new Date(obj.getTime()) as any
  if (obj instanceof Array) return obj.map(item => deepClone(item)) as any
  if (typeof obj === 'object') {
    const cloned: any = {}
    Object.keys(obj).forEach(key => {
      cloned[key] = deepClone((obj as any)[key])
    })
    return cloned
  }
  return obj
}

/**
 * é˜²æŠ–å‡½æ•°
 */
export function debounce<T extends (...args: any[]) => any>(
  func: T,
  delay: number
): (...args: Parameters<T>) => void {
  let timeoutId: NodeJS.Timeout
  
  return (...args: Parameters<T>) => {
    clearTimeout(timeoutId)
    timeoutId = setTimeout(() => func(...args), delay)
  }
}

/**
 * èŠ‚æµå‡½æ•°
 */
export function throttle<T extends (...args: any[]) => any>(
  func: T,
  limit: number
): (...args: Parameters<T>) => void {
  let inThrottle: boolean
  
  return (...args: Parameters<T>) => {
    if (!inThrottle) {
      func(...args)
      inThrottle = true
      setTimeout(() => inThrottle = false, limit)
    }
  }
}

/**
 * ç­‰å¾…æŒ‡å®šæ—¶é—´
 */
export function sleep(ms: number): Promise<void> {
  return new Promise(resolve => setTimeout(resolve, ms))
}

/**
 * æ£€æŸ¥æ˜¯å¦ä¸ºæœ‰æ•ˆçš„URL
 */
export function isValidUrl(string: string): boolean {
  try {
    new URL(string)
    return true
  } catch (_) {
    return false
  }
}

/**
 * å°†é©¼å³°å‘½åè½¬æ¢ä¸ºçŸ­æ¨ªçº¿å‘½å
 */
export function camelToKebab(str: string): string {
  return str.replace(/[A-Z]/g, letter => `-${letter.toLowerCase()}`)
}

/**
 * å°†çŸ­æ¨ªçº¿å‘½åè½¬æ¢ä¸ºé©¼å³°å‘½å
 */
export function kebabToCamel(str: string): string {
  return str.replace(/-([a-z])/g, (_, letter) => letter.toUpperCase())
}